import React, { useState, useCallback, useEffect } from 'react';
import { Box, Container, makeStyles } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import RankEditForm from './RankEditForm';
import Header from './Header';
import rankService from '../../../services/rankService';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function RankEditView() {
  const { id } = useParams();
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [rank, setRank] = React.useState();
  const [ranks, setRanks] = React.useState();
  const { t } = useTranslation();

  const getRank = useCallback(async () => {
    const data = await rankService.get(id);
    setRank(data.data.data);
  }, [isMountedRef]);

  const getRanks = useCallback(async () => {
    const data = await rankService.list();
    setRanks(data.data.data);
  }, [isMountedRef]);

  useEffect(() => {
    getRank();
    getRanks();
  }, [getRank, getRanks]);

  if (!rank) {
    return null;
  }

  if (!ranks) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Edit Rank')}>
      <Container maxWidth="lg">
        <Header />
        <Box mt={3}>
          <RankEditForm rank={rank} ranks={ranks} />
        </Box>
      </Container>
    </Page>
  );
}

export default RankEditView;
