import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useHistory } from 'react-router';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Switch,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import { useTranslation } from 'react-i18next';
import useSnackbar from '../../../hooks/useSnackbar';
import rankService from '../../../services/rankService';

const useStyles = makeStyles(() => ({
  root: {}
}));

function RankEditForm({
  className, rank, ranks, ...rest
}) {
  const classes = useStyles();
  const { successSnackbar } = useSnackbar();
  const { t } = useTranslation();
  const history = useHistory();

  return (
    <Formik
      initialValues={{
        rank: rank.rank || '',
        name: rank.name || ''
      }}
      validationSchema={Yup.object().shape({
        rank: Yup.string()
          .max(255)
          .min(1)
          .required(t('is required', { name: t('Rank') })),
        name: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Name') }))
      })}
      onSubmit={async (values, {
        resetForm, setErrors, setStatus, setSubmitting
      }) => {
        try {
          await await rankService.update(rank.id, {
            ...values,
            id: rank.id
          });
          setStatus({ success: true });
          setSubmitting(false);
          successSnackbar(t('Updated', { name: t('Rank') }));
          history.push('/ranks');
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.response.data.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values
      }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit} {...rest}>
          <Card>
            <CardContent>
              {errors.submit && (
              <Box mb={3}>
                <Alert severity="error">{errors.submit}</Alert>
              </Box>
              )}
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label={t('Name')}
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.rank && errors.rank)}
                    fullWidth
                    helperText={touched.rank && errors.rank}
                    label={t('Rank')}
                    name="rank"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.rank}
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button variant="contained" color="secondary" type="submit" disabled={isSubmitting}>
                  {t('Submit')}
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
}

RankEditForm.propTypes = {
  className: PropTypes.string,
  rank: PropTypes.object.isRequired,
  ranks: PropTypes.array.isRequired
};

export default RankEditForm;
